import React, { Component } from 'react';
import {
  CCard,
  CCardBody,
  CCardFooter,
  CForm,
  CRow,
  CFormGroup,
  CInput,
  CFormText,
  CCol,
  CLabel,
  CCardHeader
} from '@coreui/react'
import Tabletop from 'tabletop';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
const fields = ['Judul','Topik', 'Peserta', 'Tempat', 'Tanggal', 'Catatan', 'Pembuat']
const html = 'Mantab/nhehehe';
class App extends Component {
  constructor() {
    super()
    this.state = {
      data: [{Judul: "Loading..", Topik: "Loading..", Peserta: "Loading..", Tempat: "Loading..", Tanggal: "Loading..", Catatan: "Loading..", Pembuat: "Loading.."}],
      loading: false
    }
  }

  componentDidMount() {
    Tabletop.init({
      key: '1q85vwDPN6FQIm-sda1m7NPTAcjW7nIo23FCToPAW_nU',
			callback: googleData => {
        this.setState({
          data: googleData
        })
			console.log("data google sheet --->", googleData)
			},
      simpleSheet: true
    })
    this.setState({loading: true})

  }

pecahLineperLine = (string) => {
  return string.split('\n').map((item, index) => {
    return (index === 0) ? item : [<br key={index} />, item]
  })
}
render() {
    const { data } = this.state
    console.log('Apakah sedang loading ?', this.state)
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Data Catatan Rapat</h1>
        </header>
        <div>
          {
            data.map(obj => {	
              return (
          <CRow>
          <CCol lg="12">
					<CCard>
          <CCardHeader>
          <CForm action="" method="post" encType="multipart/form-data" className="form-horizontal">
          <CFormGroup row>
          <CCol md="3">
          <CLabel>Judul : </CLabel>
           </CCol>
           <CCol xs="12" md="9">
          <CInput disabled id="text-input" name="text-input" placeholder={obj.Judul} />
           </CCol>
            </CFormGroup>
          <CFormGroup row>
          <CCol md="3">
          <CLabel>Topik Rapat : </CLabel>
           </CCol>
           <CCol xs="12" md="9">
          <CInput disabled id="text-input" name="text-input" placeholder={obj.Topik} />
           </CCol>
            </CFormGroup> 
          <CFormGroup row>
          <CCol md="3">
          <CLabel>Peserta Rapat : </CLabel>
           </CCol>
           <CCol xs="12" md="9">
          <CInput disabled id="text-input" name="text-input" placeholder={obj.Peserta} />
           </CCol>
            </CFormGroup> 
          <CFormGroup row>
          <CCol md="3">
          <CLabel>Tempat Rapat : </CLabel>
           </CCol>
           <CCol xs="12" md="9">
          <CInput disabled id="text-input" name="text-input" placeholder={obj.Tempat} />
           </CCol>
            </CFormGroup>
          <CFormGroup row>
          <CCol md="3">
          <CLabel>Tanggal Rapat : </CLabel>
           </CCol>
           <CCol xs="12" md="9">
          <CInput disabled id="text-input" name="text-input" placeholder={obj.Tanggal} />
           </CCol>
            </CFormGroup>                                                
           </CForm>            
          </CCardHeader>
          <CCardBody>
           {this.pecahLineperLine(String(obj.Catatan))}
          </CCardBody>
          <CCardFooter>
          <CFormGroup row>
          <CCol md="3">
          <CLabel>Pencatat : </CLabel>
           </CCol>
           <CCol xs="12" md="9">
          <CInput disabled id="text-input" name="text-input" placeholder={obj.Pembuat} />
           </CCol>
            </CFormGroup>
          </CCardFooter>
        </CCard>
        </CCol>
        </CRow>
              )
            })
          }
        </div>
      </div>
    );
  }
}

export default App;